
//speshal
$calculated-special-primary-font-size: $special-primary-font-size;
@if internal-is-convertable($special-primary-font-size) {
	$calculated-special-primary-font-size: em($special-primary-font-size / $body-font-size)
}

$calculated-special-primary-font-size-mobile: $special-primary-font-size-mobile;
@if internal-is-convertable($special-primary-font-size-mobile) {
	$calculated-special-primary-font-size-mobile: em($special-primary-font-size-mobile / $body-font-size)
}

$calculated-special-primary-line-height: type-lead( $special-primary-font-size,$special-primary-line-height );

$calculated-special-primary-gutter-y: $special-primary-gutter-y;
@if internal-is-convertable($special-primary-gutter-y) {
	$calculated-special-primary-gutter-y: em($special-primary-gutter-y / $special-primary-font-size)
}

