//list
$calculated-body-list-offset: $body-list-offset;
@if internal-is-convertable($body-list-offset){
	$calculated-body-list-offset: em($body-list-offset / $body-font-size)
}


$calculated-body-list-offset-inline: $body-list-offset-inline;
@if internal-is-convertable($body-list-offset-inline){
	$calculated-body-list-offset-inline: em($body-list-offset-inline / $body-font-size)
}


$calculated-body-list-bullet-size: $body-list-bullet-size;
@if internal-is-convertable($body-list-bullet-size){
	$calculated-body-list-bullet-size: em($body-list-bullet-size / $body-font-size)
}

$calculated-body-list-gutter-y: $body-list-gutter-y;
@if internal-is-convertable($body-list-gutter-y){
	$calculated-body-list-gutter-y: em($body-list-gutter-y / $body-font-size)
}

$calculated-body-list-margin-right: calc(#{$body-list-offset} - #{$body-list-bullet-size});
@if internal-is-convertable($body-list-bullet-size) and internal-is-convertable($body-list-offset){
	$calculated-body-list-margin-right: em( ($body-list-offset - $body-list-bullet-size) / $body-font-size);
}

$calculated-body-list-top: calc((#{$body-line-height} - #{$body-list-bullet-size} ) * .5);
@if internal-is-convertable($body-list-bullet-size) and internal-is-convertable($body-list-offset)  and internal-is-convertable($body-line-height) {
	$calculated-body-list-top: em((($body-line-height - $body-list-bullet-size ) * .5) / $body-font-size);
}

