	/* heading bars or rules from tiny rectangles to long rules and what not. */
	@mixin decoration-bar($args:()){

		$opts: map-merge(( 
			align: '',
			color: $color-primary,
			background-image: null,
			gradient: null,
			width: 72px,
			height: 4px,
			margin: '',
			margin-top: '',
			margin-bottom: '',
			pseudo:after
		),$args);



		#{internal__pseudo-generator(map-get($opts,pseudo))}{
			
			@if map-get($opts,pseudo) != 'override' {
				content: "";
			}
			display:block;

			@if map-get($opts,width) {
				width: map-get($opts,width);
			}
			

			@if map-get($opts,height) {
				height: map-get($opts,height);
			}
			
			@if map-get($opts,margin) != '' {
				margin-top: map-get($opts,margin);
				margin-bottom: map-get($opts,margin);
			}@else{
				@if map-get($opts,margin-top) != ''{
					margin-top: map-get($opts,margin-top);
				}
				@if map-get($opts,margin-bottom) != ''{
					margin-bottom: map-get($opts,margin-bottom);
				}
			}
			@include internal__margin-generator(map-get($opts,align));

			@if map-get($opts,gradient) {
				@include gradient(map-get($opts,gradient));
			}@else if map-get($opts,background-image) {
				background-image: map-get($opts,background-image);
			}@else{

				@if map-get($opts,color) != ''{
					background-color: map-get($opts,color);
				}
			}

			@content;
		}
	}

		// @todo a work in progress
		@mixin zigzag($args: ()){
			$defaults: (
				pseudo: after, // duh
				base-size: 15px,
				color: color(p),
				stroke-width: 3px,
				size: 16px,
				zag-number: 5,
				align: '',
				margin-top: '',
				margin-bottom: '',

			);
			$opts: map-merge($defaults,$args);

			$size: map-get($opts,size);
			$stroke: map-get($opts,stroke-width);
			$color: map-get($opts,color);

			@include decoration-bar((
				width: ($size * map-get($opts,zag-number)) + ($stroke * 2.25),
				height: $size,
				align: map-get($opts,align),
				margin-top: map-get($opts,margin-top),
				margin-bottom: map-get($opts,margin-bottom),
				pseudo: map-get($opts,pseudo),
				color: transparent,
				background-image: (
					repeating-linear-gradient(
						135deg,
						$color 0,
						$color #{ percentage($stroke / $size)},
						transparent #{ percentage($stroke / $size)},
						transparent #{ percentage(.25) },
						transparent #{ percentage(.25) },
						transparent 100%
					),
					repeating-linear-gradient(
						-135deg,
						$color 0,
						$color #{ percentage($stroke / $size)},
						transparent #{ percentage($stroke / $size)},
						transparent #{ percentage(.25) },
						transparent #{ percentage(.25) },
						transparent 100%
					),
					repeating-linear-gradient(
						-45deg,
						transparent 0,
						transparent #{ percentage((($size * .25) - $stroke ) / $size)},
						$color #{ percentage((($size * .25) - $stroke ) / $size)},
						$color #{ percentage(.25) },
						transparent #{ percentage(.25) },
						transparent 100%
					),
						
					repeating-linear-gradient(
						45deg,
						transparent 0,
						transparent #{ percentage((($size * .25) - $stroke ) / $size)},
						$color #{ percentage((($size * .25) - $stroke ) / $size)},
						$color #{ percentage(.25) },
						transparent #{ percentage(.25) },
						transparent 100%
					))
			)){
				background-size: $size $size;
				background-position: 
					#{$stroke} #{( $size * .5 ) + ($stroke * .5)},
					#{$stroke} #{( $size * .5 ) + ($stroke * .5)},
					#{($size * .5) + ($stroke)} #{($size * -.5)  + ($stroke * .5)} #{','}
					#{($size * .5) + ($stroke)} #{($size * -.5) + ($stroke * .5)};
				

				@if $autoprefix == true {

					-webkit-clip-path: polygon(
						0 calc(100% - #{$stroke}),
						#{$stroke} 100%,
						calc(100% - #{$stroke}) 100%,
						100% calc(100% - #{$stroke}),
						100% #{$size - $stroke}, 
						calc(100% - #{$size - $stroke}) 0,
						#{$size - $stroke} 0,
						0 #{$size - $stroke},
					);
				}
				
				clip-path: polygon(
					0 calc(100% - #{$stroke}),
					#{$stroke} 100%,
					calc(100% - #{$stroke}) 100%,
					100% calc(100% - #{$stroke}),
					100% #{$size - $stroke}, 
					calc(100% - #{$size - $stroke}) 0,
					#{$size - $stroke} 0,
					0 #{$size - $stroke},
				);

				@content;
			};
		};

	//gradient background
	//@TODO make it kwan support stacked gradient backgrounds
	@mixin gradients($args:()) {

		$opts: map-merge((
			add-important: false,
			autoprefix: true,
			use-fallback: true,
			use-shorthand: true,
			gradients: ()
		),$args);

		
		$shorthand-image: '';
		$shorthand-color: '';

		$important: '';

		@if(map-get($opts,add-important) == true) {
			$important: ' !important';
		}

		@if(map-get($opts,use-shorthand) == false) {
			$shorthand-image: '-image';
			$shorthand-color: '-color';
		}

		$background-stack: ();

		@each $gradient-args in map-get($opts,gradients) {

			@if(type-of($gradient-args) == 'map'){
				$gradient: map-merge((
					//legacy
					color1: $color-accent,
					color2: $color-primary,
					color1-offset: 0%,
					color2-offset:100%,
		
					angle: -45deg,
					type: linear,
					colors: (
						// (
						//	 color:,
						//	 offset:
						// )
					)
				),$gradient-args);
	
	
				$points: '';
				$fallback: '';
	
				@if length(map-get($gradient,colors)) > 0 {
					$fallback: map-get(nth(map-get($gradient,colors),1),color);
		
					@each $color in map-get($gradient,colors) {
						$points: #{$points +', ' + ' '+ map-get($color,color) +' '+ map-get($color,offset)};
					}
					
				}@else{
					$fallback: map-get($gradient,color1-offset);
					$points: #{', ' + map-get($gradient,color1) +' '+ map-get($gradient,color1-offset) +', '+ map-get($gradient,color2) +' '+ map-get($gradient,color2-offset)};
				}
	
				$background-stack: append(
					$background-stack,
					internal__gradient_get_vals(
						map-get($gradient,type),
						map-get($gradient,angle),
						$points,
						$fallback
					)
				)
			}
		}

		$stack-moz: '';
		$stack-webkit: '';
		$stack-native: '';

		@for $i from 1 through length($background-stack){
			$focus-stack: nth($background-stack,$i);
			@if($i == 1){
				@if(map-get($opts,use-fallback) == true) {
					background#{ $shorthand-color }: map-get($focus-stack,fallback) #{$important}; /* Old browsers */
				}
			}

			$stack-moz: unquote(
				#{ $stack-moz + map-get($focus-stack,moz)}
			);
			$stack-webkit: unquote(
				#{ $stack-webkit + map-get($focus-stack,webkit)}
			);
			$stack-native: unquote(
				#{ $stack-native + map-get($focus-stack,native)}
			);

			@if($i < length($background-stack)){
				$stack-moz:  unquote(#{ $stack-moz + ','});
				$stack-webkit:  unquote(#{ $stack-webkit + ','});
				$stack-native:  unquote(#{ $stack-native + ','});
			}
		}

		@if map-get($opts,autoprefix) != false {
			background#{ $shorthand-image }: #{$stack-moz} #{$important}; /* FF3.6-15 */
			background#{ $shorthand-image }: #{$stack-webkit} #{$important}; /* Chrome10-25,Safari5.1-6 */
		}

