		$available-units: (
			em: 1em,
			px: 1px,
			deg: 1deg,
			vw: 100vw,
			vh: 100vh,
			cm: 1cm,
			mm: 1mm,
			in: 1in,
			pt: 1pt,
			pc: 1pc,
			ex: 1ex,
			ch: 1ch,
			rem: 1rem,
			vmin: 100vmin,
			vmax: 100vmax
		);
		@if $unit {
			// @if $force-responsive-units == true {
			//	 @if unit($number) == px or unitless($number) {
			//		 @return strip-unit($number) * map-get($available-units,$unit);
			//	 }@else {
			//		 @return $number;
			//	 }
			// } @else {

				@if type-of($number) == number {
					@return strip-unit($number) * map-get($available-units,$unit);
				}@else{
					@return $number;
				}
			// }
		}
	}

	@function em($number) {
